body {
  color: #666;
  font-family: 'Open Sans', sans-serif;
  font-family: 'Titillium Web';
  font-size: 14px;
  line-height: 24px;
  font-weight: 400;
  font-weight: normal;
}

header {
  background-color: #f5f5f5;
  color: #999;
  font-size: 13px;
  padding: 5px;
}

header a {
  color: #999;
}

header span {
  margin-right: 30px;
}

nav {
  line-height: 120px;
}

nav h1 {
  font-size: 27px;
  float: left;
}

nav ul {
  float: right;
}

nav ul li {
  margin-left: 30px;
  font-size: 16px;
  vertical-align: center;
}

a {
  color: #666;
  text-decoration: none;
}

ul {
  list-style-type: none;
}

ul li {
  float: left;
}

h1, h2, h3 {
  font-family: 'Titillium Web', sans-serif;
  clear: left;
  font-weight: normal;
  color: #444;
}

.innerwidth {
  max-width: 1140px;
  margin: auto;
}

section.scope article:first-child {
  margin-left: 0;
}

section.scope article {
  width: 22%;
  float: left;
  margin-left: 4%;
}

section.scope article h2, section.scope article p {
  padding-left: 70px;
}

